import { Container, Typography, Box, Table, TableBody, TableCell, TableHead, TableRow, IconButton, Button, TextField } from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';  
import RemoveCircleOutlineIcon from '@mui/icons-material/RemoveCircleOutline';
import { Link } from "react-router-dom";
import { format } from 'date-fns';
import { useState } from 'react';


function Taulukko(props) {

    const [tunnus, setTunnus] = useState();
    const[salasana, setSalasana] = useState();

    // Kirjautuminen sisään
    const kirjauduSisaan = () => {
        props.setKirjautunut(true);
        props.setTunnus(tunnus);
    }

    // Kirjautuminen ulos
    const kirjauduUlos = () => {
        props.setKirjautunut(false);
    }


    // Järjestetään array lopetusajan perusteella (vanhin ensin)
    props.tyoPaivat.sort(function (a, b) {
        let dateA = new Date(a.pvm), dateB = new Date(b.pvm);
        return dateA - dateB
    });

    // Tuntimäärä yhteensä
    let hYhteensa = props.tyoPaivat.reduce((prev, cur) => parseFloat(prev) + cur.maaraH, 0);
    hYhteensa = parseFloat(hYhteensa);

    // Palkka yhteensä ilman alvia
    let eYhteensa = props.tyoPaivat.reduce((prev, cur) => parseFloat(prev) + cur.palkka, 0);
    eYhteensa = parseFloat(eYhteensa);

    // Palkka yhteensä alvin kanssa
    let eVerollinen = props.tyoPaivat.reduce((prev, cur) => parseFloat(prev) + cur.verollinen, 0);
    eVerollinen = eVerollinen.toFixed(2);
  

    return (

        <Container 
            maxWidth="md" 
            sx={{backgroundColor : "#87479d", 
                height : 800 }}>

            <Typography 
                variant="h3" 
                color="#2a004a" 
                sx={{fontFamily : "sans-serif", 
                    fontWeight : "bold", 
                    paddingTop : 6, 
                    paddingBottom : 4,
                    textAlign : "center" }}
            >
                Tuntiseuranta ja laskutus
            </Typography>


            {/* Näytetään taulu ja muut tiedot, jos ollaan kirjauduttu sisään */}
            { (props.kirjautunut)
                ?<Box>
                    <Typography sx={{ fontSize : 14 }}>Olet kirjautunut sisään tunnuksella: {props.tunnus}</Typography>
                    <Button 
                        onClick={kirjauduUlos}
                        variant="contained"
                        color="secondary"
                        size="small"
                        sx={{ backgroundColor : "#2a004a", color : "#e7d5f9", marginTop : 1 }}
                    >
                            Kirjaudu ulos
                    </Button>

                    <Typography variant="h5" sx={{ marginTop : 4, marginLeft : 2}}>Tunnit</Typography>

                    <Box sx={{maxWidth : 850, 
                        backgroundColor : "#e7d5f9", 
                        margin : "auto", 
                        paddingTop : 3, 
                        paddingBottom : 3, 
                        paddingLeft : 1, 
                        marginBottom : 3,
                        marginTop : 2.5 }}>

                        <Table size="small">

                            <TableHead>
                                <TableRow>
                                    <TableCell align="left">Päivämäärä</TableCell>
                                    <TableCell align="left">Määrä h</TableCell>
                                    <TableCell align="left">Klo</TableCell>
                                    <TableCell align="left">Tehtävä</TableCell>
                                    <TableCell align="left">€</TableCell>
                                    <TableCell align="left">Verollinen €</TableCell>
                                </TableRow>
                            </TableHead>

                            <TableBody>
                                { props.tyoPaivat.map( (paiva, index) => {
                                return(
                                    <TableRow key={index}>
                                        <TableCell align="left">{format(new Date(paiva.pvm), "d.M.y")}</TableCell>
                                        <TableCell align="left">{paiva.maaraH}</TableCell>
                                        <TableCell align="left">{paiva.klo}</TableCell>
                                        <TableCell align="left">{paiva.tehtava}</TableCell>
                                        <TableCell align="left">{paiva.palkka}</TableCell>
                                        <TableCell align="left">{(paiva.verollinen).toFixed(2)}</TableCell>
                                        <TableCell>
                                            <IconButton
                                                size="small"
                                                component={Link}
                                                to={`/muokkaa/${paiva.id}`} 
                                            >
                                                <EditIcon />
                                            </IconButton>
                                            <IconButton
                                                size="small"
                                                component={Link}
                                                to={`/poista/${paiva.id}`} 
                                            >
                                                <RemoveCircleOutlineIcon />
                                            </IconButton>
                                        </TableCell>
                                    </TableRow>
                                )
                                })}
                            </TableBody>
                        </Table>
                    </Box>

                    <Box sx={{ paddingLeft : 2 }}>
                        <Typography>Yhteensä h: {hYhteensa}</Typography>
                        <Typography>Yhteensä € (veroton): {eYhteensa}</Typography>
                        <Typography>Yhteensä € (verollinen): {eVerollinen}</Typography>
                    </Box>

                </Box>

                :<Box sx={{ backgroundColor : "#e7d5f9", 
                            maxWidth : 400, 
                            height : 150, 
                            margin : "auto", 
                            marginTop : 2,
                            paddingLeft : 4,
                            paddingTop : 3 }}>
                    <Typography variant="h6" sx={{ marginBottom : 2 }}>Kirjaudu sisään:</Typography>
                    <TextField 
                        variant="outlined"
                        label="Käyttäjätunnus" 
                        size="small"
                        sx={{  width : 150, backgroundColor : "#f7e3fd", marginRight : 1 }}
                        onChange={ (e) => setTunnus(e.target.value)}
                    />

                    <TextField 
                        variant="outlined"
                        label="Salasana" 
                        size="small"
                        type="password"
                        sx={{  width : 150, backgroundColor : "#f7e3fd", marginBottom : 2 }}
                        onChange={ (e) => setSalasana(e.target.value)}
                    /><br/>
    
                    <Button 
                        size="small" 
                        color="secondary"
                        onClick={kirjauduSisaan}
                    > Kirjaudu
                    </Button>
                </Box>
            }
        </Container>
    );
  }
  
  export default Taulukko;