import { Container, Typography, Box, Button, TextField } from '@mui/material';
import { Link, useHistory } from "react-router-dom";
import { useState, useRef } from 'react';
    
    
function Laheta(props) {
     
    const history = useHistory();
    const [virheilmoitus, setVirheilmoitus] = useState({});
    const email = useRef();
    const laskutettavanNimi = useRef();
    const viite = useRef();
    const eraPaiva = useRef();


    const formHandler = (e) => {

        e.preventDefault();

        let virheet = {};

        if (!laskutettavanNimi.current.value) {
            virheet = {...virheet, laskutettavanNimi : "Anna laskutettavan nimi."}
        }

        if (!viite.current.value) {
            virheet = {...virheet, viite : "Anna laskutettavan nimi."}
        }

        if (!eraPaiva.current.value) {
            virheet = {...virheet, eraPaiva : "Anna eräpäivä."}
        }

        if (!email.current.value) {
            virheet = {...virheet, email : "Anna laskutuspalvelun sähköpostiosite."}
        }


        if (Object.entries(virheet).length > 0) {
            setVirheilmoitus({...virheet});
        }
        else {

            let uusiLaskutus = {
                nimi : laskutettavanNimi.current.value,
                viite : viite.current.value,
                eraPaiva : eraPaiva.current.value,
                email : email.current.value
            }
            props.setLaskutusTiedot(uusiLaskutus);
            history.push("/vahvistus");

        }
    }
      
        return (
          
            <Container 
                maxWidth="md" 
                sx={{backgroundColor : "#e7d5f9", height : 800 }}>
            
            <form onSubmit={formHandler}>
                <Box sx={{ margin : "auto", width : 800}}>
                <Typography 
                    variant="h4" 
                    color="#2a004a" 
                    sx={{fontFamily : "sans-serif", 
                        fontWeight : "bold", 
                        paddingTop : 4, 
                        paddingBottom : 3, 
                        paddingLeft : 3 }}
                >
                    Lähetä laskutusmääräys
                </Typography>

                <TextField 
                    required
                    label="Laskutettavan nimi"
                    variant="outlined" 
                    autoFocus={true}
                    color="secondary"
                    margin="normal"
                    sx={{ width : "45%" }}
                    inputRef={laskutettavanNimi}
                    name="laskutettavanNimi"
                    error={Boolean(virheilmoitus.laskutettavanNimi)}
                    helperText={virheilmoitus.laskutettavanNimi}
                />

                <TextField 
                    required
                    variant="outlined" 
                    label="Yhteyshenkilö tai viite"
                    color="secondary"
                    margin="normal"
                    sx={{ width : "45%", marginLeft : 2 }}
                    inputRef={viite}
                    name="viite"
                    error={Boolean(virheilmoitus.viite)}
                    helperText={virheilmoitus.viite}
                />
                
                <TextField 
                    type= "date" 
                    required
                    variant="outlined"
                    color="secondary"
                    margin="normal"
                    label="Eräpäivä"
                    InputLabelProps={{ shrink: true }}
                    sx={{ width : "45%" }}
                    inputRef={eraPaiva}
                    name="eraPaiva"
                    error={Boolean(virheilmoitus.eraPaiva)}
                    helperText={virheilmoitus.eraPaiva}
                />

                <TextField 
                    required
                    variant="outlined" 
                    label="Laskutuspalvelun sähköpostiosoite"
                    color="secondary"
                    margin="normal"
                    sx={{ width : "45%", marginLeft : 2 }}
                    inputRef={email}
                    name="email"
                    error={Boolean(virheilmoitus.email)}
                    helperText={virheilmoitus.email}
                />

                </Box>


                <Button
                        variant="contained"
                        color="secondary"
                        sx={{ marginLeft : 1, 
                            marginTop: 3, 
                            width : "45%" }}
                        type="submit"
                    >
                            Lähetä
                    </Button>

                    <Button
                        variant="contained"
                        component={Link}
                        to="/"
                        sx={{ marginTop: 3, 
                            marginLeft: 1, 
                            width : "45%", 
                            backgroundColor : "#B3B3BD" }}
                    >   
                            Peruuta
                    </Button>
            </form>
            </Container>
       
        );
}
export default Laheta;