import { Container, Typography, TextField, Button, MenuItem, Box, InputLabel, Slider, FormControl, Select } from "@mui/material";
import { useRef, useState } from 'react';
import { Link, useHistory, useParams } from "react-router-dom";


function Muokkaa(props){

    const history = useHistory();
    const { id } = useParams();
    const pvm = useRef();
    const [maaraH, setMaaraH] = useState();
    const klo = useRef();
    const tehtava = useRef();
    const palkka = useRef();
    const [alv, setAlv] = useState();

    // Haetaan muokattavan päivän tiedot, jotta saadaan ne oletusarvoiksi
    const muokattavaPaiva = props.tyoPaivat.filter((paiva) => {
        return (paiva.id === id);
    })[0];


    const formHandler = (e) => {

        e.preventDefault();

        let maaraApu = parseFloat(maaraH); // Tuntimäärä desimaaliluvuksi
        let palkkaApu = parseFloat(palkka.current.value) * maaraApu; // Päivän palkka desimaaliluvuksi
        let verollinenApu = parseFloat(alv * palkkaApu); // Verollinen palkka desimaaliluvuksi

        // Haetaan oikea objekti
        let tyoPaivatApu = props.tyoPaivat.filter((paiva) => {
            if (paiva.id === id) {
                // Asetetaan uudet arvot objektiin
                return(paiva.pvm = pvm.current.value, 
                       paiva.maaraH = maaraApu,
                       paiva.klo = klo.current.value, 
                       paiva.tehtava = tehtava.current.value,
                       paiva.palkka = palkkaApu,
                       paiva.verollinen = verollinenApu);
            }
            return paiva;
        });
        props.setTyoPaivat([...tyoPaivatApu]);
        history.push("/");

    } 

    return(

        <Container
            maxWidth="md"
            sx={{ backgroundColor : "#e7d5f9", 
                height : 700, 
                padding : 5 }}
        >
            <form onSubmit={formHandler}>
                <Box sx={{ backgroundColor : "#f7e3fd", 
                    maxWidth : 700, 
                    height : 500, 
                    margin : "auto", 
                    paddingLeft : 4 }}>

                    <Typography 
                        variant="h6" 
                        sx={{paddingTop : 3, paddingBottom : 4}}
                    >
                        Muokkaa:
                    </Typography>

                    <TextField 
                        type= "date"
                        variant="outlined" 
                        color="secondary"
                        margin="normal"
                        sx={{ width : "45%" }}
                        inputRef={pvm}
                        name="pvm"
                        defaultValue={muokattavaPaiva.pvm}
                    />
                    
                    <Box sx={{width : "47%", float : "right"}}>
                    <InputLabel>Valitse tuntien määrä</InputLabel>
                        <Slider
                            sx={{width : "85%"}}
                            marks
                            min={0}
                            max={8}
                            step={0.25}
                            valueLabelDisplay="auto"
                            color="secondary"
                            value={maaraH}
                            onChange={ (e) => setMaaraH(e.target.value)}
                        >
                        </Slider>
                    </Box>

                    <TextField 
                        label="Klo"
                        variant="outlined" 
                        color="secondary"
                        margin="normal"
                        sx={{ width : "45%" }}
                        inputRef={klo}
                        name="klo"
                        defaultValue={muokattavaPaiva.klo}
                    />

                    <TextField 
                        label="Tehtävä"
                        variant="outlined" 
                        color="secondary"
                        margin="normal"
                        sx={{ width : "45%", marginLeft : 4 }}
                        inputRef={tehtava}
                        name="tehtava"
                        defaultValue={muokattavaPaiva.tehtava}
                    /><br/>

                    <TextField 
                        label="Palkka / 1h"
                        variant="outlined" 
                        color="secondary"
                        margin="normal"
                        sx={{ width : "45%" }}
                        inputRef={palkka}
                        name="palkka"
                    />

                    <FormControl>
                    <InputLabel sx={{ marginLeft: 4, marginTop : 2 }}>ALV %</InputLabel>
                        <Select 
                            onChange={ (e) => setAlv(e.target.value)}
                            label="ALV %"
                            sx={{ marginLeft: 4, 
                                marginTop : 2, 
                                width : 150 }}
                        >
                            <MenuItem value={1.24}>24 %</MenuItem>
                            <MenuItem value={1.14}>14 %</MenuItem>
                            <MenuItem value={1.10}>10 %</MenuItem>
                            <MenuItem value={1}>0 %</MenuItem>
                        </Select>
                    </FormControl>
                    <br/>

                    <Button
                        variant="contained"
                        color="secondary"
                        sx={{ marginTop: 3, 
                            width : "47%", 
                            backgroundColor : "#87479d" }}
                        type="submit"
                    >
                            Tallenna
                    </Button>

                    <Button
                        variant="contained"
                        component={Link}
                        to="/"
                        sx={{ marginTop: 3, 
                            marginLeft: 1, 
                            width : "47%", 
                            backgroundColor : "#B3B3BD" }}
                    >   
                            Peruuta
                    </Button>

                </Box>
            </form>
        </Container>
    );
}
export default Muokkaa;