import {v4 as uuid} from 'uuid';
import { Container, Typography, TextField, InputLabel, Select, Button, MenuItem, Slider, Box, FormControl } from "@mui/material";
import { useState, useRef } from 'react';
import { Link, useHistory } from "react-router-dom";


function Uusi(props) {

    const pvm = useRef();
    const [maaraH, setMaaraH] = useState();
    const klo = useRef();
    const tehtava = useRef();
    const palkka = useRef();
    const [alv, setAlv] = useState();
    const history = useHistory();
    const [virheilmoitus, setVirheilmoitus] = useState({});


    const formHandler = (e) => {

        e.preventDefault();

        let virheet = {};

        // Päivämäärä vaaditaan
        if (!pvm.current.value) {
            virheet = {...virheet, pvm : "Anna päivämäärä."}
        }

        // Kellonajat vaaditaan
        if (!klo.current.value) {
            virheet = {...virheet, klo : "Anna päivämäärä."}
        }

        // Tehtävän nimi vaaditaan
        if (!tehtava.current.value) {
            virheet = {...virheet, tunti : "Anna päivämäärä."}
        }

        let maaraApu = parseFloat(maaraH); // Tuntimäärä desimaaliluvuksi
        let palkkaApu = parseFloat(palkka.current.value) * maaraApu; // Päivän palkka desimaaliluvuksi
        let verollinenP = parseFloat(alv * palkkaApu);  // Verollinen palkka desimaaliluvuksi


        if (Object.entries(virheet).length > 0) {
            setVirheilmoitus({...virheet});
        }
        else {

            let uusi = {
                id : uuid(),
                pvm : pvm.current.value,
                maaraH : maaraApu,
                klo : klo.current.value,
                tehtava : tehtava.current.value,
                palkka : palkkaApu,
                alv : alv,
                verollinen : verollinenP
            }
            props.setTyoPaivat([...props.tyoPaivat, uusi]);
            history.push("/");
        }
    }

    return(

        <Container
            maxWidth="md"
            sx={{ backgroundColor : "#e7d5f9", 
                height : 700, 
                padding : 5 }}
        >
            <form onSubmit={formHandler}>
                <Box sx={{ backgroundColor : "#f7e3fd", 
                    maxWidth : 700, 
                    height : 500, 
                    margin : "auto", 
                    paddingLeft : 4 }}>
                
                    <Typography 
                        variant="h6" 
                        sx={{ paddingTop : 5, paddingBottom : 3 }}
                    >
                        Lisää uusi:
                    </Typography>

                    <TextField 
                        type= "date" 
                        required
                        variant="outlined" 
                        color="secondary"
                        margin="normal"
                        sx={{ width : "45%" }}
                        inputRef={pvm}
                        name="pvm"
                        error={Boolean(virheilmoitus.pvm)}
                        helperText={virheilmoitus.pvm}
                    />
                    
                    <Box sx={{width : "47%", float : "right"}}>
                    <InputLabel>Valitse tuntien määrä</InputLabel>
                        <Slider
                            sx={{width : "85%"}}
                            marks
                            min={0}
                            max={8}
                            step={0.25}
                            defaultValue={0}
                            valueLabelDisplay="auto"
                            color="secondary"
                            value={maaraH}
                            onChange={ (e) => setMaaraH(e.target.value)}
                        >
                        </Slider>
                    </Box>

                    <TextField 
                        label="Klo" 
                        required
                        variant="outlined" 
                        color="secondary"
                        margin="normal"
                        sx={{ width : "45%" }}
                        inputRef={klo}
                        name="klo"
                        error={Boolean(virheilmoitus.klo)}
                        helperText={virheilmoitus.klo}
                    />

                    <TextField 
                        label="Tehtävä" 
                        required
                        variant="outlined" 
                        color="secondary"
                        margin="normal"
                        sx={{ width : "45%", marginLeft : 4 }}
                        inputRef={tehtava}
                        name="tehtava"
                        error={Boolean(virheilmoitus.tehtava)}
                        helperText={virheilmoitus.tehtava}
                    /><br/>

                    <TextField 
                        label="Palkka / 1h" 
                        required
                        variant="outlined" 
                        color="secondary"
                        margin="normal"
                        sx={{ width : "45%" }}
                        inputRef={palkka}
                        name="palkka"
                        error={Boolean(virheilmoitus.palkka)}
                        helperText={virheilmoitus.palkka}
                    />

                    <FormControl>
                    <InputLabel sx={{ marginLeft: 4, marginTop : 2 }}>ALV %</InputLabel>
                    <Select 
                        required
                        onChange={ (e) => setAlv(e.target.value)}
                        label="ALV %"
                        sx={{ marginLeft: 4, marginTop : 2, width : 150 }}
                      >
                        <MenuItem value={1.24}>24 %</MenuItem>
                        <MenuItem value={1.14}>14 %</MenuItem>
                        <MenuItem value={1.10}>10 %</MenuItem>
                        <MenuItem value={1}>0 %</MenuItem>
                    </Select>
                    </FormControl>
                    <br/>

                    <Button
                        variant="contained"
                        color="secondary"
                        sx={{ marginTop: 3, width : "47%", backgroundColor : "#87479d" }}
                        type="submit"
                    >
                            Tallenna
                    </Button>

                    <Button
                        variant="contained"
                        component={Link}
                        to="/"
                        sx={{ marginTop: 3, marginLeft: 1, width : "47%", backgroundColor : "#B3B3BD" }}
                    >   
                            Peruuta
                    </Button>

                </Box>
            </form>
        </Container>

    );
}
export default Uusi;