import { BrowserRouter as Router, Route } from "react-router-dom";
import Taulukko from "./components/Taulukko";
import Valikko from "./components/Valikko";
import Uusi from "./components/Uusi";
import Muokkaa from "./components/Muokkaa";
import Poista from "./components/Poista";
import Laheta from "./components/Laheta";
import Vahvistus from "./components/Vahvistus";
import { useState, useEffect } from 'react';


function App() {

  const [tyoPaivat, setTyoPaivat] = useState([]);
  const [laskutusTiedot, setLaskutusTiedot] = useState({});
  const [kirjautunut, setKirjautunut] = useState(false);
  const [tunnus, setTunnus] = useState();

   // Tallennus local storageen
   const tallenna = () => {
    localStorage.setItem("paivat", JSON.stringify(tyoPaivat));
  }

  // Haku local storagesta
  const hae = () => {
    if (localStorage.getItem("paivat")) {
      setTyoPaivat(JSON.parse(localStorage.getItem("paivat")));
    }
    else {
      setTyoPaivat([]);
    }
    
  }

  // Tallennettujen tietojen haku vain silloin, kun sivu avataan ensimmäisen kerran
  useEffect(() => {
    hae();
  }, []);

  // Tallennus aina, kun tilamuuttuja muuttuu
  useEffect(() => {
    tallenna();
  }, [tyoPaivat]);


  return (

    <>
      <Router>

        {/* Komponenttia ei näytetä, jos ei ole kirjauduttu sisään*/}
        {(kirjautunut) 
          ?<Valikko/>
          :null
        }

        <Route path="/" exact>
          <Taulukko 
            tyoPaivat={tyoPaivat} 
            setTyoPaivat={setTyoPaivat} 
            tallenna={tallenna}
            hae={hae}
            kirjautunut={kirjautunut}
            setKirjautunut={setKirjautunut}
            tunnus={tunnus} 
            setTunnus={setTunnus}
          />
        </Route>

        {(kirjautunut)
          ?<Route path="/uusi">
            <Uusi 
              tyoPaivat={tyoPaivat} 
              setTyoPaivat={setTyoPaivat}/>
          </Route>
          : null
        }

        {(kirjautunut)
          ?<Route path="/muokkaa/:id">
            <Muokkaa 
              tyoPaivat={tyoPaivat} 
              setTyoPaivat={setTyoPaivat}/>
          </Route>
          : null
        }

        {(kirjautunut)
          ?<Route path="/poista/:id">
            <Poista 
              tyoPaivat={tyoPaivat} 
              setTyoPaivat={setTyoPaivat}/>
          </Route>
          : null
        }

        {(kirjautunut)
          ?<Route path="/laheta">
            <Laheta 
              tyoPaivat={tyoPaivat} 
              setTyoPaivat={setTyoPaivat}
              laskutusTiedot={laskutusTiedot}
              setLaskutusTiedot={setLaskutusTiedot}
              tunnus={tunnus} 
              setTunnus={setTunnus}/>
          </Route>
          : null
        }

        {(kirjautunut)
          ?<Route path="/vahvistus">
            <Vahvistus 
              tyoPaivat={tyoPaivat} 
              setTyoPaivat={setTyoPaivat}
              laskutusTiedot={laskutusTiedot}
              setLaskutusTiedot={setLaskutusTiedot}
              tunnus={tunnus}
              setTunnus={setTunnus}/>
          </Route>
          : null
        }
      </Router>
    </>

  );
}
export default App;