import { AppBar, Toolbar, CssBaseline, Button } from '@mui/material';
import { Link } from "react-router-dom";


function Valikko() {
    
    return(

        <CssBaseline>
            <AppBar 
                position="sticky" 
                sx={{ backgroundColor : "#2a004a", margin : "auto", maxWidth : 900}}
            >
                <Toolbar>
                    <Button
                        component={Link}
                        to="/"
                        size="large"
                        color="inherit"
                        sx={{color : "#ce84e1"}}
                    >Etusivu
                    </Button>
                    <Button 
                        component={Link}
                        to="/uusi"
                        size="large"
                        color="inherit"
                        sx={{color : "#ce84e1"}}
                        >Lisää uusi suoritus
                        </Button>
                    <Button 
                        component={Link}
                        to="/laheta"
                        size="large"
                        color="inherit"
                        sx={{color : "#ce84e1"}}
                    >Lähetä laskutusmääräys
                    </Button>
                </Toolbar>
            </AppBar>
        </CssBaseline>

    );
}
export default Valikko;