import { Button, Typography, Box } from '@material-ui/core';
import { Link, useHistory, useParams } from 'react-router-dom';
import { format } from 'date-fns';

function Poista(props) {

    const { id } = useParams();
    const history = useHistory();

    // Haetaan poistettavan päivän tiedot
    const poistettavaPaiva = props.tyoPaivat.filter((paiva) => {
        return (paiva.id === id);
    })[0];

    const formHandler = (e) => {

        e.preventDefault();

        let tyoPaivatApu = props.tyoPaivat.filter((paiva) => {
            return (paiva.id !== id);
        });
        props.setTyoPaivat([...tyoPaivatApu]);
        history.push("/");
    
    }

    return (
     
            <Box sx={{ backgroundColor : "#e7d5f9", 
                        maxWidth : 900, 
                        height : 800, 
                        margin : "auto", 
                        padding : 30 }}>

            <form onSubmit={formHandler}>

                <Typography 
                    variant="h6"
                    style={{ paddingTop : 10 }}
                >
                    Haluatko varmasti poistaa seuraavan suorituksen:
                </Typography>

                <Typography
                    style={{ paddingTop : 15, paddingBottom : 20, fontWeight : "bold" }}
                >
                    {format(new Date(poistettavaPaiva.pvm), "d.M.y")}, {poistettavaPaiva.klo}, {poistettavaPaiva.maaraH} h: {poistettavaPaiva.tehtava}?
                </Typography>

                <Button 
                    type="submit"
                    variant="contained"
                    size="small"
                    color="secondary"
                    style={{ marginRight : 5, backgroundColor : "#87479d", color : "#f7e3fd" }}
                >
                    Poista
                </Button>

                <Button 
                    component={Link}
                    to="/"
                    variant="contained"
                    size="small"
                    sx={{backgroundColor : "#B3B3BD"}}
                >
                    Peruuta
                </Button>

            </form>

            </Box>
    )
}
export default Poista;