import { Container, Typography, Box, Button, Table, TableRow, TableHead, TableBody, TableCell } from '@mui/material';
import { format } from 'date-fns';
import { Link } from "react-router-dom";

function Vahvistus(props) {

    // Laskurit
    let hYhteensa = props.tyoPaivat.reduce((prev, cur) => parseFloat(prev) + cur.maaraH, 0);
    let eYhteensa = props.tyoPaivat.reduce((prev, cur) => parseFloat(prev) + cur.palkka, 0);
    let verollinen = props.tyoPaivat.reduce((prev, cur) => parseFloat(prev) + cur.verollinen, 0);
    verollinen = verollinen.toFixed(2);

    const tyhjennaLista = () => {
        props.setTyoPaivat([]);
    }

    return(

        <Container maxWidth="md" sx={{ backgroundColor : "#e7d5f9", height : 800 }}>

            <Box sx={{ paddingTop : 4, 
                paddingBottom : 5, 
                paddingLeft : 4 }}>

                <Typography variant="h5" sx={{ paddingBottom : 3 }}>Laskutusmääräys lähetetty osoitteeseen {props.laskutusTiedot.email}</Typography>
                <Typography>Yhteensä h: {hYhteensa}</Typography>
                <Typography>Yhteensä € (veroton): {eYhteensa}</Typography>
                <Typography sx={{ marginBottom : 2}}>Yhteensä € (verollinen): {verollinen}</Typography>
        
                <Typography>Laskutettava: {props.laskutusTiedot.nimi}</Typography>
                <Typography>Viite: {props.laskutusTiedot.viite}</Typography>
                <Typography>Maksun saaja: {props.tunnus}</Typography>
                <Typography>Eräpäivä: {format(new Date(props.laskutusTiedot.eraPaiva), "d.M.y")}</Typography>

                <Table size="small" sx={{ width : 700, marginTop : 3 }}>

                    <TableHead>
                        <TableRow>
                            <TableCell align="left" width="100">Pvm</TableCell>
                            <TableCell align="left" width="100">H</TableCell>
                            <TableCell align="left" width="100">Klo</TableCell>
                            <TableCell align="left" width="200">Tehtävä</TableCell>
                            <TableCell align="left" width="100">€</TableCell>
                            <TableCell align="left" width="150">ALV %</TableCell>
                            <TableCell align="left" width="300">Verollinen €</TableCell>
                        </TableRow>
                    </TableHead>

                    <TableBody>
                        { props.tyoPaivat.map( (paiva, index) => {
                        return(
                            <TableRow key={index}>
                                <TableCell align="left" width="100">{format(new Date(paiva.pvm), "d.M.y")}</TableCell>
                                <TableCell align="left" width="100">{paiva.maaraH}</TableCell>
                                <TableCell align="left" width="100">{paiva.klo}</TableCell>
                                <TableCell align="left" width="200">{paiva.tehtava}</TableCell>
                                <TableCell align="left" width="100">{paiva.palkka}</TableCell>
                                <TableCell align="left" width="150">{((paiva.alv-1)*100).toFixed(0)}</TableCell>
                                <TableCell align="left" width="100">{(paiva.verollinen).toFixed(2)}</TableCell>
                            </TableRow>
                        )
                        })}
                    </TableBody>
                </Table>
            </Box>

            <Button
                variant="contained"
                component={Link}
                color="secondary"
                to="/"
                sx={{ marginLeft: 4, backgroundColor : "#87479d" }}
                onClick={tyhjennaLista}
            >   
                Tyhjennä lista ja palaa etusivulle
            </Button>

        </Container>
    );
}
export default Vahvistus;